import React from 'react';

class OpenGraph extends React.Component {
    render() {
        return (
            <>
                <meta property="og:title" content="Kanami Community" />
                <meta property="og:type" content="website" />
                <meta property="og:image" content="https://avatars.githubusercontent.com/u/77463052" />
                <meta property="og:description" content="Kanami Community - это сообщество Kanami бота, созданное с любовью :3" />
                <meta property="og:site_name" content="KanamiCommunity" />
                <meta property="theme-color" content="#f54242" />
            </>
        );
    };
};

export default OpenGraph;